#calcGrid {
  margin: auto;
  display: grid;
  grid-template-rows: repeat(7, 1fr);
  grid-template-columns: repeat(4, 1fr);
  padding: 0.75em;
  grid-gap: 5px;
  width: 30vw;
  height: 50vw;
  min-width: 300px;
  min-height: 500px;
  max-width: 600px;
  max-height: 1000px;
  border: 2px solid slategray;
  border-radius: 3px;
  background-color: rgb(50, 49, 56);
  grid-template-areas: 
  "a a a a"    /*equation    */
  "b b b b"    /*output      */
  "c c d e"    /*AC / *      */
  "f g h i"    /*7 8 9 -     */
  "j k l m"    /*4 5 6 +     */
  "n o p q"    /*1 2 3 =     */
  "r r s q";    /* 0  .      */
}

.gridItem {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(97, 161, 221);
  border: 2px outset rgb(200, 242, 255);
  border-radius: 6px;
  font-weight: bolder;
}
.gridItem:not(.noHilite):hover{
  border: 2px solid white;
  cursor: pointer;
}
#input {
  grid-area: a;
  background-color: rgb(255, 255, 242);
  border: 1px inset darkgoldenrod;
  border-radius: 2px;
  margin-bottom: 0.75em;
  font-weight: normal;
}
#display {
  grid-area: b;
  background-color: rgb(255, 255, 162);
  border: 1px inset rgb(255, 183, 0);
  border-radius: 2px;
  margin-bottom: 0.75em;
  font-size: 1.2rem;
}
#clear {
  grid-area: c;
  background-color: rgb(255, 104, 104);
}
#divide, #multiply, #subtract, #add {
  background-color: rgb(27, 26, 51);
  color: white;
}
#divide {
  grid-area: d;
}
#multiply {
  grid-area: e;
}

#seven {
  grid-area: f;
}
#eight {
  grid-area: g;
}
#nine {
  grid-area: h;
}
#subtract {
  grid-area: i;
}
#four {
  grid-area: j;
}
#five {
  grid-area: k;
}
#six {
  grid-area: l;
}
#add {
  grid-area: m;
}
#one {
  grid-area: n;
}
#two {
  grid-area: o;
}
#three {
  grid-area: p;
}

#equals {
  grid-area: q;
  background-color: rgb(127, 47, 255);
}

#zero {
  grid-area: r;
}
#decimal {
  grid-area: s;
}




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
